.header-wrapper {
  background-color: #ddd;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 20px;
  padding-right: 20px;
  color: #000080;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.header-title {
  font-size: 1.15em;
}

.header-section-left {
	height: 45.625px;
}

.header-section-left img {
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}

.header-section-right {
	width: 30px;
	visibility: hidden;
}


